import React from "react";
import { useTranslation } from "react-i18next";
import Modal from "react-modal";

Modal.setAppElement("#root");

const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "20%",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
    transition: "transform 0.3s ease-out",
    backgroundColor: "#fff",
    overflowY: "auto",
    maxHeight: "80%", // ajustez la hauteur maximale selon vos besoins
  },
  overlay: {
    backgroundColor: "rgba(0, 0, 0, 0.5)",
    zIndex: 9999,
  },
};

const ConfidentialModale = () => {
  const { t, i18n } = useTranslation("en", { useSuspense: false });

  // modal for legal notices
  let subtitle;
  const [modalIsOpen, setIsOpen] = React.useState(false);

  function openModal() {
    setIsOpen(true);
  }

  function afterOpenModal() {
    // references are now sync'd and can be accessed.
    subtitle.style.color = "#f00";
  }

  function closeModal() {
    setIsOpen(false);
  }

  return (
    <div>
      <div>
        <p>
          {t("contact.checkbox")}
          <span className="modale-btn link-mod" onClick={openModal}>
            {t("contact.politic")}
          </span>
        </p>
        <Modal
          isOpen={modalIsOpen}
          onAfterOpen={afterOpenModal}
          onRequestClose={closeModal}
          style={customStyles}
          contentLabel="Example Modal"
        >
          <div className="modale-container" id="footer-modale-container">
            {i18n.language === "fr" ? (
              <div className="confidential">
                <h2 ref={(_subtitle) => (subtitle = _subtitle)}>
                  Politique de confidentialité
                </h2>
                <button onClick={closeModal}>
                  <i className="fa-regular fa-circle-xmark"></i>
                </button>
                <div>
                  <h3>Introduction</h3>
                </div>
                <div>
                  Devant le développement des nouveaux outils de communication,
                  il est nécessaire de porter une attention particulière à la
                  protection de la vie privée. C’est pourquoi, nous nous
                  engageons à respecter la confidentialité des renseignements
                  personnels que nous collectons.
                </div>

                <h3>Collecte des renseignements personnels</h3>
                <div>
                  <ul>
                    <li>- Nom</li>
                    <li>- Adresse électronique</li>
                  </ul>
                </div>
                <div>
                  Les renseignements personnels que nous collectons sont
                  recueillis au travers de formulaires et grâce à
                  l’interactivité établie entre vous et notre site Web. Nous
                  utilisons également, comme indiqué dans la section suivante,
                  des fichiers témoins et/ou journaux pour réunir des
                  informations vous concernant.
                </div>

                <h3>Formulaires et interactivité:</h3>
                <div>
                  Vos renseignements personnels sont collectés par le biais de
                  formulaire, à savoir :
                </div>
                <div>
                  <ul>
                    <li>- Formulaire de contact</li>
                  </ul>
                </div>
                <div>
                  Nous utilisons les renseignements ainsi collectés pour les
                  finalités suivantes :
                </div>
                <div>
                  <ul>
                    <li>- Correspondance</li>
                  </ul>
                </div>
                <div>
                  Nous utilisons les renseignements ainsi collectés pour les
                  finalités suivantes :
                </div>
                <div>
                  <ul>
                    <li>- Contact</li>
                  </ul>
                </div>

                <h3>Droit d’opposition et de retrait</h3>
                <div>
                  Nous nous engageons à vous offrir un droit d’opposition et de
                  retrait quant à vos renseignements personnels.
                </div>
                <div>
                  Le droit d’opposition s’entend comme étant la possiblité
                  offerte aux internautes de refuser que leurs renseignements
                  personnels soient utilisés à certaines fins mentionnées lors
                  de la collecte.
                </div>
                <div>
                  Le droit de retrait s’entend comme étant la possiblité offerte
                  aux internautes de demander à ce que leurs renseignements
                  personnels ne figurent plus, par exemple, dans une liste de
                  diffusion.
                </div>
                <div>
                  <strong>
                    Pour pouvoir exercer ces droits, vous pouvez :
                  </strong>
                </div>
                <div>Code postal : 42660</div>
                <div>Courriel : lolita.danancher@wanadoo.fr</div>
                <div>Téléphone : 0681177109</div>
                <div>Section du site web : Contact</div>

                <h3>Droit d’accès</h3>
                <div>
                  Nous nous engageons à reconnaître un droit d’accès et de
                  rectification aux personnes concernées désireuses de
                  consulter, modifier, voire radier les informations les
                  concernant.
                </div>
                <div>
                  <strong>L’exercice de ce droit se fera :</strong>
                </div>
                <div>Code postal : 42660</div>
                <div>Courriel : lolita.danancher@wanadoo.fr</div>
                <div>Téléphone : 0681177109</div>
                <div>Section du site web : Contact</div>

                <h3>Sécurité</h3>
                <div>
                  Les renseignements personnels que nous collectons sont
                  conservés dans un environnement sécurisé. Les personnes
                  travaillant pour nous sont tenues de respecter la
                  confidentialité de vos informations.
                </div>
                <div>
                  Pour assurer la sécurité de vos renseignements personnels,
                  nous avons recours aux mesures suivantes :
                </div>
                <div>
                  <ul>
                    <li>Effacement des données après un an.</li>
                  </ul>
                </div>
                <div>
                  Nous nous engageons à maintenir un haut degré de
                  confidentialité en intégrant les dernières innovations
                  technologiques permettant d’assurer la confidentialité de vos
                  transactions. Toutefois, comme aucun mécanisme n’offre une
                  sécurité maximale, une part de risque est toujours présente
                  lorsque l’on utilise Internet pour transmettre des
                  renseignements personnels.
                </div>

                <h3>Législation</h3>
                <div>
                  Nous nous engageons à respecter les dispositions législatives
                  énoncées dans :
                </div>
                <div>
                  Législation: Règlement (UE) 2016/679 du Parlement européen et
                  du Conseil du 27 avril 2016, relatif à la protection des
                  personnes physiques à l'égard du traitement des données à
                  caractère personnel et à la libre circulation de ces données,
                  et abrogeant la directive 95/46/CE (règlement général sur la
                  protection des données).
                </div>
              </div>
            ) : (
              <div className="confidential">
                <h2 ref={(_subtitle) => (subtitle = _subtitle)}>
                  Privacy Policy
                </h2>
                <button onClick={closeModal}>
                  <i className="fa-regular fa-circle-xmark"></i>
                </button>
                <div>
                  <h3>Introduction</h3>
                </div>
                <div>
                  In light of the development of new communication tools, it is
                  essential to pay special attention to the protection of
                  privacy. Therefore, we are committed to respecting the
                  confidentiality of the personal information we collect.
                </div>

                <h3>Collection of Personal Information</h3>
                <div>
                  <ul>
                    <li>- Name</li>
                    <li>- Email address</li>
                  </ul>
                </div>
                <div>
                  The personal information we collect is gathered through forms
                  and through the interaction between you and our website. We
                  also use cookies and/or logs, as explained in the following
                  section, to gather information about you.
                </div>

                <h3>Forms and Interactivity:</h3>
                <div>
                  Your personal information is collected through forms, namely:
                </div>
                <div>
                  <ul>
                    <li>- Contact form</li>
                  </ul>
                </div>
                <div>
                  We use the collected information for the following purposes:
                </div>
                <div>
                  <ul>
                    <li>- Correspondence</li>
                  </ul>
                </div>
                <div>
                  We use the collected information for the following purposes:
                </div>
                <div>
                  <ul>
                    <li>- Contact</li>
                  </ul>
                </div>

                <h3>Right to Object and Withdrawal</h3>
                <div>
                  We are committed to providing you with the right to object to
                  and withdraw your personal information.
                </div>
                <div>
                  The right to object means the opportunity for internet users
                  to refuse the use of their personal information for certain
                  purposes mentioned during collection.
                </div>
                <div>
                  The right to withdrawal means the opportunity for internet
                  users to request the removal of their personal information,
                  for example, from a mailing list.
                </div>
                <div>
                  <strong>To exercise these rights, you can:</strong>
                </div>
                <div>Postal Code: 42660</div>
                <div>Email: lolita.danancher@wanadoo.fr</div>
                <div>Phone: 0681177109</div>
                <div>Website Section: Contact</div>

                <h3>Right of Access</h3>
                <div>
                  We are committed to recognizing the right of access and
                  rectification to individuals who wish to consult, modify, or
                  delete their information.
                </div>
                <div>
                  <strong>This right can be exercised by:</strong>
                </div>
                <div>Postal Code: 42660</div>
                <div>Email: lolita.danancher@wanadoo.fr</div>
                <div>Phone: 0681177109</div>
                <div>Website Section: Contact</div>

                <h3>Security</h3>
                <div>
                  The personal information we collect is stored in a secure
                  environment. The people working for us are required to respect
                  the confidentiality of your information.
                </div>
                <div>
                  To ensure the security of your personal information, we
                  implement the following measures:
                </div>
                <div>
                  <ul>
                    <li>Data deletion after one year.</li>
                  </ul>
                </div>
                <div>
                  We are committed to maintaining a high level of
                  confidentiality by incorporating the latest technological
                  innovations to ensure the confidentiality of your
                  transactions. However, as no mechanism provides maximum
                  security, there is always some risk when transmitting personal
                  information over the Internet.
                </div>

                <h3>Legislation</h3>
                <div>
                  We are committed to complying with the legislative provisions
                  outlined in:
                </div>
                <div>
                  Legislation: Regulation (EU) 2016/679 of the European
                  Parliament and of the Council of 27 April 2016 on the
                  protection of natural persons with regard to the processing of
                  personal data and on the free movement of such data, and
                  repealing Directive 95/46/EC (General Data Protection
                  Regulation).
                </div>
              </div>
            )}
          </div>
        </Modal>
      </div>
    </div>
  );
};

export default ConfidentialModale;
